* {
	--left-width: 250px;
}

.basic-info {
	padding: var(--global-padding) 20px;
	min-width: var(--left-width);
	background-color: #fbbd0826;
	border-radius: 8px;
	margin: 0 20px 20px 0;
}

.pokemon-layout {
	justify-content: space-around;
	flex-grow: 1;
	padding: 0;
	text-transform: capitalize;

	.metadata {
		flex-grow: 1;

		.left {
			align-items: center;
		}

		.pokemon-container {
			display: flex;
			justify-content: space-between;
			white-space: nowrap;

			.label {
				padding-right: 20px;
			}
		}
	}

	.pokemon-container {

		.title {
			margin: 100px;
    		font-size: 20px;
    		font-weight: bold;
    		text-align: center;
		}

		&.sprites-element {
			justify-content: center;
			background-color: #8080802b;
    		padding: 20px;
    		border-radius: 8px;
    		margin: 0 0 20px 0;
    		flex-grow: 1;
    		flex-wrap: wrap;
    		align-self: stretch; 

    		.flex-column {
    			justify-content: center;
    		}
		}

		.label {
			font-weight: bold;
			text-align: center;
		}

		.abilities,
		.held-items {
			padding: var(--global-padding);
			justify-content: center;
			flex-wrap: wrap;

			img {
				object-fit: contain;
			}
		}

		.types,
		.abilities, {
			
			> div:not(:last-child) {
				margin-right: 3px;
			}
		}

		.stat {
			padding: var(--global-padding) 20px;
			white-space: nowrap;
			width: var(--left-width);
			background-color: #2185d00f;
    		border-radius: 8px;
    			
			th {
				text-align: left;
			}

			th:not(:first-child) {
				padding-left: 10px;
			}

			td:not(:first-child) {
				text-align: center;
			}
		}
	}

	.ability-move {
		padding: 0 10px 10px 10px;
		flex-grow: 1;
		justify-content: center;
		background-color: #8080802b;
	    margin: 0 20px;
	    border-radius: 4px;

		.dialogs {
			justify-content: space-evenly;
			padding: 20px;

			button {
				background-color: #000000e0;
				color: white;
			}
		}
	}

	.moves {
	    display: flex;
    	flex-wrap: wrap;
    	padding: var(--global-padding);

    	> div {
    		width: fit-content;
    		margin-right: 10px;
    		margin-bottom: 	5px;
    	}
	}
}

.loader-container {
	height: 100vh;
    position: absolute;
    top: 50%;
    width: 100vw;
}

button {
	text-transform: capitalize !important;
    padding: 5px 10px;
    border-radius: 15px ;
    border: none;
    outline: none;
    color: white;

    &:hover {
    	cursor: unset;
    }

    &.grass {
		background-color: var(--grass-color);
	}

	&.normal {
		background-color: var(--normal-color);
	}

	&.fire {
		background-color: var(--fire-color);
	}

	&.water {
		background-color: var(--water-color);  
	}

	&.electric {
		background-color: var(--electric-color);
	}

	&.ice {
		background-color: var(--ice-color);
	}

	&.fighting {
		background-color: var(--fighting-color);
	}

	&.poison {
		background-color: var(--poison-color);
	}

	&.ground {
		background-color: var(--ground-color);
	}

	&.flying {
		background-color: var(--flying-color);
	}

	&.psychic {
		background-color: var(--psychic-color);
	}

	&.bug {
		background-color: var(--bug-color);
	}

	&.rock {
		background-color: var(--rock-color);
	}

	&.ghost {
		background-color: var(--ghost-color);
	}

	&.dragon {
		background-color: var(--dragon-color);
	}

	&.dark {
		background-color: var(--dark-color);
	}

	&.steel {
		background-color: var(--steel-color);
	}

	&.fairy {
		background-color: var(--fairy-color);
	}
}

button {
	margin: 5px !important;
}

.modal {

	.flex {
		justify-content: center;
	    align-items: center;
	}

	.held-effect {
		height: fit-content;
	}

	img {
		padding-right: 20px;
	}
}

.description-box {
	min-width: 150px;
    padding: 10px;
    background-color: rgba(0,0,0,.95);
    color: white;
    border-radius: 4px;

	> * {
		justify-content: space-between !important;
		text-transform: capitalize;
	}
}

.description-effect {
	padding: 0 20px;
	flex-grow: 1;
    text-align: center;
}

.games > div:not(:last-child) {
	padding-bottom: 20px;
}
	.label-container {
		background-color: #1bff0029;
		border-radius: 4px;
		padding: 10px;
		margin: 5px;
		align-self: stretch;
		display: flex;
		align-items: center;

		.label {
			min-width: 150px;
		}
	}

	.location {
		align-items: center;
		padding-top: 5px;
		border-radius: 4px;
		flex-wrap: wrap;
		margin: 5px;
		max-width: 100%;
		border: 1px solid #21ba266b;
	}

	.region-name {
   		padding: 0 10px 10px 10px;
	}

	.encounters {
		width: 100%;
   		flex-wrap: wrap;

		.label {
			color: crimson;
			padding-right: 20px;
		}
	}

	.encounters > div:not(:last-child) {
		padding-right: 20px;
	}

	.encounter {
		white-space: nowrap;
	    width: fit-content;
	    padding: 10px;
	    border-radius: 8px;
	    margin: 0 10px;

		.flex {
			justify-content: space-between;
		}
	}

.region-container {
	flex-wrap: wrap;
	max-width: 100%;
	justify-content: center;
	overflow: hidden;
}

.name-container {
	display: flex;
	justify-content: center;
	padding: 10px 0;
	align-items: baseline;

	.label {
		font-weight: bold;
    	font-size: 19px;
	}

	.id {
		color: grey;
    	font-weight: bold;
	}
}

.type-container {
	display: flex;
	justify-content: center;
	padding-bottom: 20px;
}

.flavor-text {
	width: 100%;

	.flex {
	    padding: 20px 20px 20px 0;
	    background-color: #181a3312;
	    border-radius: 4px;
	    margin: 5px;
	    align-items: center;
	}
	.text {
		text-transform: none;
	}
	
	.title {
		margin: 100px;
		font-size: 20px;
		font-weight: bold;
		text-align: center;
	}

	.label {
		font-weight: bold;
		min-width: 170px;
		text-align: center;
	}
}

.metadata-2 {
    background-color: #e6666626;
    padding: 15px 20px;
    border-radius: 8px;
    align-self: stretch;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .label {
    	padding-right: 30px;
    }
}

.height,
.weight,
.gender-rate .flex {
	display: flex;
	justify-content: space-between;
}

.special {
	justify-content: unset !important;
}

.evolution-chain {
	background-color: #8080802b;
    border-radius: 8px;
    padding: 20px 20px 0 20px;
    margin: 20px 0;
    justify-content: center;
    text-align: center;
    align-items: center;

    > .label {
    	padding-bottom: 10px;
    }
    
    .pokemon {
    	padding: 0 20px;
    	border-radius: 4px;
    	margin-bottom: 20px;

    	&:hover {
    		cursor: pointer;
    		background-color: #8080801a;
    	}
    }

    .pokemon-evolutions {
    	width: fit-content;
    	flex-wrap: wrap;
    	justify-content: center;
    }

    .id {
    	font-weight: bold;
    	color: grey;
    }

    .name {
    	font-weight: 700;
		color: #000486;
    	font-weight: bold;
    	font-size: 18px;
    }

    .button-container {
    	justify-content: center;
    }
}

img {
	object-fit: contain;
}

.dmg-element {
	justify-content: center;
    flex-wrap: wrap;

	th {
		padding-bottom: 10px;
	}

	.multiplier {
		text-transform: lowercase;
		color: #00000080;
		text-align: center;
	}

	th,td {
		text-align: left;
		padding: 5px 0 5px 20px;
	}

	.dmg-type {
		padding: 5px;
		border-radius: 4px;
		width: 40px;
		align-items: center;
		margin: 5px;

		&.grass {
			background-color: #7ac74c40;
			color: var(--grass-color);
		}

		&.normal {
			background-color: #a8a77a40;
			color: var(--normal-color);
		}

		&.fire {
			background-color: #ee813040;
			color: var(--fire-color);		
		}

		&.water {
			background-color: #6390f040;
			color: var(--water-color);		  
		}

		&.electric {
			background-color: #f7d02c40;
			color: var(--electric-color);		  		
		}

		&.ice {
			background-color: #96d9d640;
			color: var(--ice-color);		  		
		}

		&.fighting {
			background-color: #c32d2840;
			color: var(--fighting-color);		  		
		}

		&.poison {
			background-color: #a33ea140;
			color: var(--poison-color);		  		
		}

		&.ground {
			background-color: #e2bf6540;
			color: var(--ground-color);		  				
		}

		&.flying {
			background-color: #a98ff340;
			color: var(--flying-color);		  				
		}

		&.psychic {
			background-color: #f9558740;
			color: var(--psychic-color);		  				
		}

		&.bug {
			background-color: #a6b91a40;
			color: var(--bug-color);		  				
		}

		&.rock {
			background-color: #b6a13640;
			color: var(--rock-color);		  				
		}

		&.ghost {
			background-color: #74579840;
			color: var(--ghost-color);		  						
		}

		&.dragon {
			background-color: #6f35fc40;
			color: var(--dragon-color);		  						
		}

		&.dark {
			background-color: #70574640;
			color: var(--dark-color);	
		}

		&.steel {
			background-color: #b7b7ce40;
			color: var(--steel-color);			
		}

		&.fairy {
			background-color: #d685ad40;
			color: var(--fairy-color);			
		}
	}
}

.dmg-title {
	text-align: center;
    padding-bottom: 10px;
}

.dmg-component {
	padding: 10px;
    background-color: #8080802b;
    border-radius: 8px;
}

.vertical-text {
	text-orientation: sideways;
    writing-mode: tb-rl;
    height: 60px;
    text-align: center;
    width: fit-content;
}

.pokedex-entry {
	text-align: center;
	text-transform: none;
    background: #8080802b;
    border-radius: 8px;
    padding-top: 20px;

	.entry {
		font-weight: bold;
    	padding: 10px 20px 20px;
	}
}

.ui.bottom.attached.segment.active.tab {
	border: none !important;
}

.item {
	text-transform: capitalize !important;
	text-align: center !important;
	border: none !important;

	&:hover {
		background-color: #8080802b !important; 
	}
}

.active.item {
	background-color: #8080802b !important; 
}

.habitat {
	text-align: center;
	padding-bottom: 20px;
}

.special-locations {
	background: #27ff001f;
    border-radius: 8px;
    padding: 20px;
    margin-top: 20px;
}

.stretched.twelve.wide.column {
	height: fit-content;
}

.four.wide.column {
	overflow: hidden !important;
}

.ui.menu {
	overflow-x: scroll !important;
}

.pokemon-games {
	padding: 20px;
	text-align: center;
}

.tabs-element {
	margin-top: 10px;
	min-height: 100vh;
}

.tab-moves-element {
	margin-top: 20px;
	background-color: #8080802b !important; 
	border-radius: 8px;
	padding: 0 20px 20px;
	justify-content: space-around;

	> * {
		max-width: 300px;
	}

	.title {
		text-align: center;
		margin: 20px 0;
	}

	.tab-moves {
		margin-top: 20px;
		flex-wrap: wrap;
		margin: 0 auto;
		justify-content: center;

		.tab-move {
			white-space: nowrap;
			padding: 10px;
		}

		button {
			background-color: #000000e0;
			color: white;
		}
	}
}

.detail-level {
	width: 20px;
}

.detail {
	align-items: center;
}

button:hover {
	opacity: .9;
	cursor: pointer;
}

.cancel:hover {
	opacity: 1;
	cursor: unset;
}

button {
	white-space: nowrap;
}

@media (max-width: 1000px) {
	.metadata {
		flex-direction: column;
		align-items: center;

		.pokemon-container.flex-column {
			width: 100%;
		}
	}

	.sprites-element {
		width: 100%;
	}

	.basic-info {
		margin: 0px 20px 20px;
		width: 100%;
	}

	.stat {
		width: 100% !important;
	}

	.pokemon-layout .pokemon-container .stat th {
		text-align: center;
	}
	.pokemon-layout .pokemon-container .stat th:first-child {
		text-align: left;
	}

	.pokemon-layout .ability-move {
		margin: 20px 0;
		width: 100%;
		padding: 0;

		.dialogs {
			flex-direction: column;
		}
	}
    .pokemon-container.flex-column.special {
    	margin-top: 20px;
    }

	.top-container {
		flex-direction: column;
		align-items: center;
	}
}