* {
	--focus-blue: #9ec0ff;	
}

.dropdown {
	padding: var(--global-padding);
	text-transform: capitalize;
	border: 1px solid grey;
	display: flex;
	align-items: center;
	margin: 20px 0;

	.search {
		color: #8080804d;
		font-size: 18px;
		margin-right: 10px;
	}

	.options-container {
		left: -1px;
		width: 100%;
		top: 100%;
		background-color: white;
    	border-radius: 0 0 8px 8px;
		border: 1px solid var(--focus-blue);
		border-top: unset;
		overflow: hidden;
		box-sizing: content-box;

		.options {
			max-height: 150px;
			overflow-y: scroll;
		    font-size: 16px;
		    width: 100%;
		    padding-right: 17px;
		    overflow-x: hidden;


		    .option {
		    	width: 100%;
		    	padding: var(--global-padding);

		    	&:hover, 
		    	&#selected {
		    		background-color: #dcdcdc2b;
		    	}
		    }
		}
	}

	input {
	    width: 100%;
		text-transform: capitalize;
		border: unset;

	    &:focus {
	    	outline: none;
	    }
	}
}


.dropdown-id {
	color: grey;
	padding-right: 10px;
}